<template>
  <div
    ref="bar"
    class="progress-bar"
    :style="{
      '--progress-bar-height': height
    }"
  >
    <div
      ref="progress"
      class="progress-bar__line"
    />
  </div>
</template>

<script>
import { computed, onMounted, ref, watch } from 'vue'

export default {
  name: 'ProgressBar',
  props: {
    height: {
      type: String,
      default: '4px'
    },
    stepsCount: {
      type: Number,
      default: 1
    },
    step: {
      type: Number
    }
  },
  setup (props) {
    const progress = ref()
    const bar = ref()
    const fullWidth = ref(100)
    const stepWidth = ref(0)
    const progressWidth = computed(() => {
      const width = props.step * stepWidth.value
      return width > fullWidth.value ? fullWidth.value : width
    })

    watch(() => props.step, () => {
      changeProgressWidth()
    })

    onMounted(() => {
      getWidth()
      changeProgressWidth()
    })

    const changeProgressWidth = () => {
      progress.value.style.width = progressWidth.value + '%'
    }
    const getWidth = () => {
      stepWidth.value = fullWidth.value / props.stepsCount
    }
    return {
      bar,
      progress
    }
  }
}
</script>

<style lang="scss">
.progress-bar {
  height: var(--progress-bar-height);
  background-color: #E0E0E0;

  &__line {
    height: 100%;
    background-color: map-get($colors, "primary");

    transition: width 200ms linear;
  }
}
</style>
