<template>
  <Select
    v-bind="$attrs"
    v-model="lang"
    :options="selectOptions"
    hide-on-click
    :show-arrow="showArrow"
    class="lang"
  >
    <template #selected>
      <div v-if="!$slots.default" class="lang__selected">
        <Icon :name="lang.label"/>
      </div>
      <slot v-else :lang="lang" />
    </template>
    <template v-slot:option="{ option }">
      <div v-if="!$slots.option" class="d-flex justify-content-center align-items-center">
        <Icon :name="option.label" />
        <Text type="uppercase" ml="8px" weight="500" size="14px">{{ option.value }}</Text>
      </div>
      <slot name="option" :option="option" />
    </template>
  </Select>
</template>

<script>
import { useI18n } from '@/shared/plugins/i18n'
import { ref, watch } from 'vue'
import moment from 'moment'
import Icon from '@/shared/components/Icon/Icon'
import Select from '@/shared/components/UIKit/Select/Select'
import Text from '@/shared/components/UIKit/Text/Text'

export default {
  name: 'Lang',
  inheritAttrs: true,
  components: { Text, Select, Icon },
  props: {
    showArrow: {
      type: Boolean,
      default: false
    }
  },
  setup () {
    const { locale } = useI18n()

    const selectOptions = ref([
      {
        label: 'us',
        value: 'en',
        text: 'English'
      },
      {
        label: 'ru',
        value: 'ru',
        text: 'Russian'
      },
      {
        label: 'cn',
        value: 'cn',
        text: 'Chinese'
      }
    ])

    const lang = ref(selectOptions.value[0])

    watch(lang, nv => {
      moment.locale(nv.value)
      locale.value = nv.value
    })

    return {
      lang,
      selectOptions
    }
  }
}
</script>

<style lang="scss">
.lang {
  margin-left: 14px;

  &__selected {
    width: 20px;
  }
  .select {

    &__options {
      width: unset;
      box-shadow: 0 12px 24px rgba(0, 0, 0, 0.2);

      &__item {
        box-sizing: border-box;
        border: 2px solid transparent;
        border-bottom: 1px solid $borderColor;
        padding: 10px 16px;

        &_selected {
          border-bottom: 2px solid map-get($colors, "primary");
          border-color: map-get($colors, "primary") !important;
          background: #FFF3EA;
        }
      }
    }
  }
}
</style>
